@tailwind base;
@tailwind components;
@tailwind utilities;

$header-height: 220px;

$color-bg: #edebf6;
$color-bg-light: white;
$color-light-text: rgb(111, 111, 111);
$color-text-base: #231C23;
$base-padding: 16px;
$half-padding: calc($base-padding / 2);

$search-bar-height: 64px;
$footer-height: 64px;

$side-container-size: 64px;
$main-container-size: 100%;
$main-container-margin-top:calc(2.5em + 10px);

$center-container-width: 80%;

$footer-margin:.5vw;
$footer-font-size:1em;

$mexinput-border-width:3px;
$queryblock-height:250px;

$signup-color:#9F92F4;
$info-background:#D4CCF2;

$input-color:#D9D9D9;

$interaction-container-padding:5px;
$interaction-container-item-margin:10px;

$history-container-padding:-999px;
$history-animation-time:.5s;
$history-item-active-size:1.25em;

$form-container-background:white;

$history-query__text-overflow-width:20vw;

$adaptive-footer-font-size:12px;

body {
  background-color: $color-bg;
  color: $color-text-base;
}

.body-wrapper {
  @apply w-full flex justify-center;
}

.content-wrapper {
  width: 100%;
  display: flex;
  align-items: flex-end;
}

.settings-button {
  width: 2.5em;
  height: 2.5em;
  border-radius: $search-bar-height;
  background-color: $color-text-base;
  color: $color-bg-light;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: fixed;
  right: 0;
  margin-top:  10px;
  margin-right:10px;
  z-index: 3;
}

.settings-icon {
  transition: transform 100ms ease-in-out;
}

.settings-button:hover > .settings-icon {
  transform: rotate(30deg);
}

.settings-button--active > .settings-icon {
  transform: rotate(30deg);
}

.main-container {
  width: $main-container-size;
  background-color: $color-bg;
  padding: $base-padding;
}

.settings-container{
  margin-top:$main-container-margin-top
}

.main-container__response {
  padding: $half-padding !important;
}

.you-searched {
  font-size: 10px;
  font-style: italic;
  opacity: 75%;
}

.search-query {
  font-size: 14px;
  max-width: 30vw;
  white-space: normal;
  word-wrap: break-word;
}

.mathmex-response {
  background-color: $color-bg-light;
  border-radius: $base-padding;
  padding: $base-padding;
  position: relative;
}

.mathmex-response__mobile {
  background-color: $color-bg-light;
  border-radius: $base-padding;
  padding: $base-padding $base-padding 0px $base-padding;
  position: relative;
}

.mathmex-response--title {
  font-size: 24px;
  font-weight: bold;
  width: 90%;
  padding-top: 12px;
}

.mathmex-response--relevance {
  cursor: pointer;
  user-select: none;
  text-transform: uppercase;
  background-color: $color-text-base;
  font-size: 12px;
  width: 96px;
  text-align: center;
  font-weight: bold;
  display: inline-block;
  padding: $half-padding $half-padding;
  border-radius: 0 $base-padding 0 $half-padding;
  position: absolute;
  top: 0;
  right: 0;
}

.mathmex-response--relevance__high {
  @apply text-green-400;
}

.mathmex-response--relevance__medium {
  @apply text-orange-400;
}

.mathmex-response--relevance__low {
  @apply text-red-400;
}

.mathmex-response--link {
  cursor: pointer;
  user-select: none;
  text-decoration: underline;
  color: #862286;
  font-size: 12px;
  display: inline-block;
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.mathmex-response--text {
  text-align: justify;
  margin: 48px 64px 24px 64px;
  position: relative;
}

.mathmex-response--image-container{
  display: flex;
  justify-content: center;
  align-items: center;
}

.mathmex-response--text__mobile {
  text-align: justify;
  margin: 48px 0px 12px 0px;
  position: relative;
}

.mathmex-response--quote-left {
  position: absolute;
  font-size: 48px;
  opacity: 20%;
  top: -24px;
  left: -64px;
}

.mathmex-response--quote-right {
  position: absolute;
  font-size: 48px;
  opacity: 20%;
  bottom: -24px;
  right: -64px;
}

.mathmex-response--thumbsup {
  @apply mb-4 opacity-50 hover:text-green-500 cursor-pointer;
}

.mathmex-response--thumbsup__active {
  @apply opacity-75 text-green-500;
}

.mathmex-response--thumbsdown {
  @apply opacity-50 hover:text-red-500 cursor-pointer;
}

.mathmex-response--thumbsdown__active {
  @apply opacity-75 text-red-500;
}

.query-block-wrapper {
  @apply w-full;
}

.horizontal-rule {
  @apply w-full flex items-center justify-center border-b border-black border-opacity-10;
}

.next-result {
  @apply h-10 my-2 px-2 flex justify-center items-center bg-black bg-opacity-0 hover:bg-opacity-10 rounded-md text-sm cursor-pointer select-none;
}

.side-container {
  width: $side-container-size;
  height: 100%;
  padding: $base-padding;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 100ms ease-in-out;
}

.side-container__search {
  width: 25% !important;
  text-align: right;
  opacity: 1;
  visibility: visible;
}

.main_container__search {
  height: 75% !important;
}

.content-wrapper:hover > .side-container {
  visibility: visible;
  opacity: 1;

  transition: visibility 200ms, opacity 100ms ease-in-out;
}

.header-bar {
  // position:fixed
  @apply max-w-screen-lg flex justify-center /*fixed*/;
  width: $center-container-width;
  background-color: $color-bg;
  z-index: 2;
  flex-direction: column
}

.centered-wrapper {
  @apply w-full max-w-screen-lg flex flex-col;
}

.response-container {
  width: $center-container-width;
  // margin-top: $header-height;
  margin-bottom: $footer-height;
  padding-top: $base-padding;
}

.bottom-bar {
  @apply w-full max-w-screen-xl fixed bottom-0 flex items-center justify-center;
  background-color: $color-bg;
  height: $footer-height;
  padding: $base-padding;
  font-weight: 500;
}

.footer-option {
  font-size: $footer-font-size;
  margin-left: $footer-margin;
  margin-right: $footer-margin;
  cursor: pointer;
  color: $color-light-text;
}

.footer-option:hover {
  color: rgb(60, 60, 60);
}

.footer-copyright {
  font-size: $footer-font-size;
  margin-left: $footer-margin;
  margin-right: $footer-margin;
  color: $color-text-base;
}

.search-bar {
  height: $search-bar-height;
  width: $main-container-size;
  bottom: 0;
  left: 0;
  padding: 0 $base-padding $base-padding;
}

.sources-dropdown {
  position: absolute;
  height: $search-bar-height;
  width: $side-container-size;
  right: 0;
  bottom: 0;
  background-color: $color-bg;
  padding: 0 0 $base-padding;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-container {
  width: 100%;
  // This was the initial height but my 118px will need a fix
  // height: calc($header-height - $search-bar-height);
  height: 118px;
  top: 0;
  left: 0;
  padding: $base-padding;
  display: flex;
  justify-content: center;
}

.logo-text {
  height: 1em;
  width: fit-content;
}

.mathmex-logo {
  background-image: url("../public/banner.png");
  width: 500px;
  // height: 50%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.search-button {
  width: 96px;
  height: 100%;
  background-color: $color-text-base;
  color: white;
  border-radius: 50vh 0px 0px 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.math-input-full {
  height: 100%;
  width: 100%;
  border-radius: 50vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

math-field {
  width: 100%;
  border-radius: 50vh;
  display: block;
  margin-left: 16px;
}

math-field:focus-within {
  outline: none;
}

.data-source {
  @apply relative w-40 h-52 p-4 rounded-md cursor-pointer hover:shadow-sm hover:scale-105 transform transition-all flex-shrink-0;
  background-color: rgba($color-text-base, 0.05);
}

.data-source--inactive:hover {
  @apply bg-opacity-5;
}

.data-source--active {
  @apply bg-opacity-100;
  background-color: $color-bg-light;
}

.data-source__check {
  @apply absolute bottom-4 right-4 opacity-0 transition-all;
}

.data-source--inactive:hover > .data-source__check {
  @apply opacity-50;
}

.data-source--active > .data-source__check {
  @apply opacity-100 text-amber-500;
}

/* scrollbar */
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-border-radius: 10px;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}

::-webkit-scrollbar-thumb:window-inactive {
  background: rgba(255, 255, 255, 0.3);
}


.login-container{
  // width: fit-content;
  background-color: $info-background;
  min-height: fit-content;
  border-radius: 10px;
  border-color: black;
  border-width: 2px;
  display: flex;
  flex-direction: column;
  padding:15px;
}

.login-header{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.login-hr{
  border:1px black solid;
  margin-bottom: 8px;
}

.signup-container{
    width: fit-content;
    min-height: fit-content;
    border-radius: 10px;
    border-color: black;
    border-width: 2px;
    display: flex;
    flex-direction: row;
    background-color: $form-container-background;
    
}

.signup-info{
  background-color: $info-background;
  margin: 10px;
  padding:8px;
  border-radius: 8px;
  border-color: black;
  border-width: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.signup-info > hr{
  border:1px black solid;
  width: 80%;
  margin-bottom: 8px;
}

.signup-info > b{
  font-size: 2em;
}


.signup-right{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 8px;
}

.signup-form{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.signup-submit{
  background: $signup-color !important;
  padding:6px;
  border-radius: 4px;
  color:white;
  cursor: pointer;
}

.login-submit{
  padding:calc(6px - 1px);
  border-radius: 4px;
  border-color:black;
  border-width: 1px;
  cursor: pointer;
}

.signup-input{
  border-width:1px;
  margin-bottom:20px;
  border-color:black;
  border-radius:8px;
  padding: 5px;
}

.signup-form > input{
  background:none;
  outline: none;
  background-color: $input-color;
}

.signup-password-wrapper{
  border-width:1px;
  border-color:black;
  border-radius:8px;
  margin-bottom:20px;
  background-color: $input-color;
}
.signup-password{
  width: calc(100% - 2.25em);
  background:none;
  outline: none;
  padding: 5px;
  border-right-width: 1px;
  border-color:black
}
.password-eye{
    display:inline-block;
    margin-left:1px;
    cursor: pointer;
}
.signup-error {
  margin-top:10px;
  color: red;
  -webkit-animation: fadeinout 4s linear forwards;
  animation: fadeinout 4s linear forwards;
}

@-webkit-keyframes fadeinout {
0%,100% { opacity: 0; }
25%,50% { opacity: 1; }
}

@keyframes fadeinout {
0%,100% { opacity: 0; }
50% { opacity: 1; }
}

.interaction-container{
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  padding-left: $interaction-container-padding;
  padding-top:$interaction-container-padding;
}

.interaction-container--item{
  margin-right: $interaction-container-item-margin;
}

.email{
  text-decoration: underline;
  text-underline-offset: 5px;
  margin-right:10px;
}

.rotate-left{
  transform: rotate(-180deg);
}

.rotate-90{
  transform: rotate(90deg);
}

.history-animation{
  transition: $history-animation-time;
}

.history-wrapper{
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  height: 50vh;
  transition: $history-animation-time;
  z-index: 999;
 
}

.history-content{
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.history-items{
  width: 25vw;
  height: 100%;
  border-top-right-radius: 10vh;
  border-bottom-right-radius: 10vh;
  background-color: $input-color;
  direction: rtl;
  text-align: right;
  overflow-y:auto;
  padding-right:2em;
  padding-top:2em;
  padding-bottom:2em;
  padding-left:1em;
  border-width: 1px;
  border-color: black;
  -webkit-box-shadow: 5px 5px 15px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 5px 5px 15px 0px rgba(0,0,0,0.75);
  box-shadow:5px 5px 15px 0px rgba(0,0,0,0.75);
}

.history-item{
 transition: $history-animation-time;
 cursor: pointer;
 padding: 5px;
 border-radius:15px;
}

.history-item:hover{
 
  font-size: $history-item-active-size;
  background-color: #b9b4b4;
}

.history__chevron{
  cursor: pointer;
}

.history-item__chevron{
  cursor: pointer;
  transition: $history-animation-time;
}


.history-item__chevron:hover{
  cursor: pointer;
  @apply rotate-90
}

.history__title{
  writing-mode:vertical-rl;
  position: absolute;
  top:0;
  right: 0;
  margin-top:2em;
}

.history-query{
  border: 1px solid black;
  border-radius:5px;
  padding: 5px;
  margin-bottom: 10px;
  direction: ltr;
  background-color:#e1b2e5;
}

.history-query__text{
  max-width:$history-query__text-overflow-width;
  white-space: normal;
  overflow-wrap: anywhere;
  display: flex;
  justify-content: start;
  cursor: pointer;
}

.history-query__info{
  border-bottom:1px solid black;
  margin-bottom: 5px;
}

.history-query__options--item{
  margin-right: 5px;
  cursor: pointer;
}

.history-query__options--copy{
  -webkit-transition: color 1000ms linear;
  -ms-transition: color 1000ms linear;
  transition: color 1000ms linear;
}

.history-query__options{
  border-top:1px solid black;
  margin-top: 5px;
  padding-top:5px;
}
// for allowing mathjax to line wrap
mjx-math.MJX-TEX {
  white-space: normal !important;
  line-height: 0.5 !important;
}

.forgot-password{
  color: #4343ff;
}

.forgot-password:hover{
  color: purple;
  text-decoration: underline;
}

.password-container{
  border: 1px solid black;
  border-radius: 10px;
  background-color: $info-background;
  padding:10px;

}
.password-error{
  margin-top: 5px;
  color: red;
  text-align: center;
}

.confirm-container{
  border: 1px solid black;
  border-radius: 10px;
  background-color: $info-background;
  padding:10px;
}

.confirm-submit{
  background-color: $input-color;
}

.reset-success{
  border: 1px solid black;
  padding:5px;
  border-radius: 5px;
}

.form-background{
  background-image: url("../public/background.png");
  background-size: cover;
}

.absolute-center{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
//----------------------------------------------------------------------

.mexinput-container{
  max-width: 1024px;
  width: 1024px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  border-color:#edebf6;
  border-width: 3px;
}

.mexinput-container:focus-within{
  border-color:#231C23 !important;
  border-width: 3px;
}

.multi-mathfield-container{
  margin-top: 20px;
  width: 100%;
  max-width: 1024px;
  height: 15vh;
  overflow-y: auto;
}

.mex-textarea-button-container{
  position: absolute;
  bottom: 15px;
  right: 10px;
}

.mex-textarea{
  word-wrap: break-word;
	overflow-wrap: break-word;
	white-space: normal;	
  outline: 0px solid transparent;
}

[data-placeholder]:empty:before{
  content: attr(data-placeholder);
  color: #888;
  font-style: italic;
}



.mex-textarea-button{
  margin-left: 15px;
  cursor: pointer;
}





.equation-symbol{
  cursor: pointer;
  padding:1px;
  border-radius:3px;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  border-width: 1px;
  border-color:black;
  background-color:grey;
  color:white
}


.mf-container{
  padding-bottom: 10px;
}

.mex-input-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.response-container-wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.mathmex-response__failure{
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 16px;
  border-color: red;
  border-width:$mexinput-border-width ;
  height: $queryblock-height;
}

.mathmex-response__failure--content{
  font-size: 3.35rem;
}

.logout-container{
  padding-bottom: 100px;
}


.mathmex-response__failure--retry{
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: transform 0.5s ease-in-out;
  cursor: pointer;
}
.mathmex-response__failure--retry:hover{
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  transition: transform .5s ease-in-out;
  transform: rotate(90deg);
}

.mathmex-response__loading{
  border-radius: 16px;
  @apply shimmer
}

.shimmer{
  @keyframes placeholder-shimmer {
    0%   { background-position:  100%; }
    100% { background-position: -100%; }
  }
    animation: 3s cubic-bezier(.25,.50,.75,.50) infinite placeholder-shimmer;
    background-image: linear-gradient(to right, 
      rgba(0,0,0,0.00)   0%, 
      rgba(0,0,0,0.25)  25%, 
      rgba(0,0,0,0.00)  50%, 
      rgba(0,0,0,0.25)  75%, 
      rgba(0,0,0,0.00) 100%
    );
    background-size: 200%;
    height: $queryblock-height; width: 100%;
}

//---------------------------ADAPTIVE STYLES----------------------  


@media only screen and (max-width: 768px) {
  $center-container-width: 70%;
  $history-query__text-overflow-width:50vw;

  .history-query__text {
    max-width: $history-query__text-overflow-width;
  }
  .header-bar {
    width: $center-container-width;
  }

  .response-container {
    width: $center-container-width;
  }
  
}
// Mobile Devices
@media only screen and (max-width: 640px) {
   $adaptive-footer-font-size:12px;

  .logo-container{
    height: 100px;
  }

  .mex-textarea-trash:active{
    color: red;
  }

  .login-container{
    width: calc(100vw - 40px);
    margin-left:20px;
    margin-right:20px;
  }
  .signup-container{
    width: calc(100vw - 10px);
    margin-left:5px;
    margin-right:5px;
    flex-direction: column;
    
   
  }
  .history-items{
    width: 60vw;
  }  
  .history__title{
    margin-right: .5em;
  }
}

@media only screen and (max-width: 320px) {

  $history-query__text-overflow-width:60vw;
  .history-query__text {
    max-width: $history-query__text-overflow-width;
  }
  .history-items{
    width: 80vw;
  }  
}

@media only screen and (min-width: 768px) {

  .footer-option {
    font-size: $adaptive-footer-font-size;
  }
  
  .footer-copyright {
    font-size: $adaptive-footer-font-size;
  }
}

// Very Small Screens
@media only screen and (max-width: 440px) {
  $adaptive-footer-font-size:9px;
  .mathmex-response__failure--content {
    font-size: 2rem;
  }
 .footer-option {
   font-size: $adaptive-footer-font-size;
  }
 
 .footer-copyright {
   font-size: $adaptive-footer-font-size;
  }
}
// Desktop
@media only screen and (min-width: 640px) {
  .mex-textarea-trash:hover{
    color: red;
  }
}